
.qjDetail {
  padding: 10px 15px;
}
.qjDetail .qgProcess {
    width: 100%;
    font-size: .9em;
    text-align: right;
    color: #999999;
    line-height: 2;
}
.qjDetail .qgProcess label {
      float: left;
      margin-right: 1em;
      min-width: 4em;
      color: #999999;
      text-align: justify;
      text-align-last: justify;
}
.qjDetail .qgProcess span {
      display: block;
      overflow: hidden;
      word-break: normal;
      word-wrap: break-word;
}



