
@charset "UTF-8";
/** 重置vue */
.vux-1px-b:after {
  border-color: #D9D9D9;
  color: #D9D9D9;
}
.vux-1px-t:before {
  border-color: #D9D9D9;
  color: #D9D9D9;
}
.vux-no-group-title, .vux-no-group-title {
  margin-top: 0 !important;
}
.weui-cell:before {
  left: 0 !important;
}
.vux-cell-primary {
  text-align: left !important;
}
.vux-cell-box {
  color: #999;
}
.weui-cells:after {
  border-bottom: 0px solid #D9D9D9 !important;
}
.vux-cell-box:not(:first-child):before {
  left: -10px !important;
}
.weui-dialog__bd:first-child {
  padding: 0px !important;
}
.weui-wepay-flow, .weui-wepay-flow-auto {
  padding: 0 !important;
  position: absolute;
  left: -20px;
  top: 15px;
  height: 100%;
}
.weui-wepay-flow__li .weui-wepay-flow__state {
  width: 20px !important;
  height: 20px !important;
  border-radius: 50% !important;
  line-height: 20px !important;
  left: -2px !important;
}
/** 用户故事*/
.htmlfiel {
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
  color: #999;
  font-size: 17px !important;
  line-height: 24px;
  padding: 10px 15px;
  overflow-x: scroll;
}
.htmlfiel span {
    font-size: 17px !important;
}
/** 分配给 */
.user_list {
  line-height: 0.8rem;
  text-align: center;
}
.detail {
  z-index: 100;
  padding-bottom: 1.2rem;
  box-sizing: border-box;
  background: #fff;
}
/** 底部按钮 */
.btn {
  width: 100%;
  height: 46px;
  background: #fff;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 10;
}
.btn span {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
            flex: 1;
    text-align: center;
    line-height: 46px;
}
.btn .agree {
    background-color: #09bb07;
    color: #fff;
}
.btn .reject {
    background-color: #db321c;
    color: #fff;
}
.btn .transfer {
    background-color: #f6f6f6;
}
/** 转办人员信息*/
.transfer_info {
  width: 100%;
}
.transfer_info .transfer_title {
    height: 0.6rem;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
}
.transfer_info .transfer_title span {
      -webkit-box-flex: 1;
      -webkit-flex: 1;
              flex: 1;
      line-height: 0.6rem;
}
.transfer_info .transfer_list {
    height: 4rem;
    overflow-y: scroll;
}
.transfer_info .transfer_list li {
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
}
.transfer_info .transfer_list li span {
        -webkit-box-flex: 1;
        -webkit-flex: 1;
                flex: 1;
        line-height: 0.6rem;
}
.transfer_info .transfer_list li.choiced {
      background-color: #ccc;
}
/** 工作流*/
.process {
  width: 100%;
  background: #F0F2F5;
  /** 审批意见*/
}
.process .agree_status {
    background: #FFF;
    padding-bottom: 0.2rem;
}
.process .agree_status .allInfo {
      margin-left: 0.8rem;
      position: relative;
}
.process .agree_status .allInfo .info {
        border-bottom: 1px solid #ccc;
        padding: 0.2rem;
        position: relative;
        vertical-align: middle;
}
.process .agree_status .allInfo .info p {
          display: block;
          margin-left: 0.6rem;
}
.process .agree_status .allInfo .info p span {
            display: inline-block;
            font-size: 0.3rem;
            margin-bottom: 0.1rem;
            margin-right: 0.4rem;
}
.process .agree_status .allInfo .info p .node_name {
            float: right;
            width: 3rem;
}
.process .agree_status .allInfo:last-child {
        margin-bottom: 0.2rem;
}
.process .agree_status .allInfo:last-child .weui-wepay-flow__line {
          display: none;
}
.process .choice {
    width: 100%;
    margin-top: 0.9rem;
    background: #fff;
}
.process .choice ul li {
      font-size: 0.3rem;
      border-bottom: 1px solid #d9d9d9;
      position: relative;
}
.process .choice ul li .iconfont {
        position: absolute;
        left: 0.4rem;
        top: 0.3rem;
        width: 0.4rem;
        height: 0.4rem;
}
.process .choice ul li .title {
        position: absolute;
        left: 0;
        top: -0.64rem;
        width: 100%;
        border-bottom: 1px solid #d9d9d9;
        padding-left: 0.4rem;
        background: #fff;
        display: none;
        margin-bottom: 0;
        height: 0.64rem;
        line-height: 0.64rem;
}
.process .choice ul li:first-child .title {
        display: block;
}
.process .choice ul li .choice_content {
        padding-left: 1rem;
}
.process .choice ul li .choice_content p {
          width: 100%;
          line-height: 0.6rem;
          padding-right: 0.2rem;
}
.process .choice ul li .choice_content p em {
            float: right;
            font-style: normal;
            margin-right: 0.2rem;
}



